.image {
  /* position: relative; */
  align-content: center;
  left: 0;
  max-width:100%;
  height:100%;
  width:100%;
  margin: 0 auto;
  overflow: hidden;
}
.figure{
  align-content: center;
  position: relative;
  padding-bottom: 2rem;
}
@media (min-width: 90rem){
  .figure{
    float:right;
  }
}
@media (min-width: 80rem){
  .sidebarcontainer{
    margin-left:-10%;
	display: flex;
	flex-direction: row;
  }
}
@media (min-width: 80rem){
  .floatright{
    float:right;
    padding-left:1rem;
  }
}
.sidebar{
  background-color: rgb(146, 203, 153);
  padding-left:1rem;
  padding-right:1rem;
  float:right;
  margin-left: .5rem;
  max-width: 55%;
}
@media (min-width: 80rem){
  .sidebar{
	margin-right:-2%;
	display: block
};
  
}
@media (max-width: 80rem){
  .sidebar{
    max-width: 100%;
    width:100%;
  }
}
/* .sidebar:type */
.sidebar>h2:first-child{
  margin-top:1.25rem;
}
.sidebar::before{
  margin-left:.75rem;
}
.rainbow{
		animation: rainbow 2.5s linear;
		animation-iteration-count: infinite;
    font-size: x-large;
}

@keyframes rainbow-bg{
		100%,0%{
			background-color: rgb(255,0,0);
		}
		8%{
			background-color: rgb(255,127,0);
		}
		16%{
			background-color: rgb(255,255,0);
		}
		25%{
			background-color: rgb(127,255,0);
		}
		33%{
			background-color: rgb(0,255,0);
		}
		41%{
			background-color: rgb(0,255,127);
		}
		50%{
			background-color: rgb(0,255,255);
		}
		58%{
			background-color: rgb(0,127,255);
		}
		66%{
			background-color: rgb(0,0,255);
		}
		75%{
			background-color: rgb(127,0,255);
		}
		83%{
			background-color: rgb(255,0,255);
		}
		91%{
			background-color: rgb(255,0,127);
		}
}

@keyframes rainbow{
		100%,0%{
			color: rgb(255,0,0);
		}
		8%{
			color: rgb(255,127,0);
		}
		16%{
			color: rgb(255,255,0);
		}
		25%{
			color: rgb(127,255,0);
		}
		33%{
			color: rgb(0,255,0);
		}
		41%{
			color: rgb(0,255,127);
		}
		50%{
			color: rgb(0,255,255);
		}
		58%{
			color: rgb(0,127,255);
		}
		66%{
			color: rgb(0,0,255);
		}
		75%{
			color: rgb(127,0,255);
		}
		83%{
			color: rgb(255,0,255);
		}
		91%{
			color: rgb(255,0,127);
		}
}
